import React from 'react';
import {
  MenuItem,
  SideNavMenu,
  TopNavMenu,
  PageSubMenu,
  Layout,
  Top,
  LeftSide,
  RightSide,
  Content,
} from '@tiarebalbi/fortaleza-ui-theme';

import ButtonWrapper from './components/ButtonWrapper';

const App = () => (
  <Layout>
    <Top>
      <TopNavMenu>
        <MenuItem text="Página Inícial" href="/"/>
        <MenuItem text="Config." href="/config"/>
        <MenuItem icon="user" href="/user"/>
      </TopNavMenu>
    </Top>
    <Content>
      <LeftSide>
        <SideNavMenu>
          <MenuItem icon="home" text="Home" href="/"/>
          <MenuItem icon="user" text="Usuários" href="/"/>
          <MenuItem icon="api" text="Clientes" href="/"/>
          <MenuItem icon="usergroup-add" text="Funcionários" href="/"/>
        </SideNavMenu>
      </LeftSide>
      <RightSide>
        <PageSubMenu title="Home">
          <a href="/">Option 1</a>
          <a href="/">Option 2</a>
          <a href="/">Option 3</a>
        </PageSubMenu>
        <ButtonWrapper/>
      </RightSide>
    </Content>
  </Layout>
);

export default App;



